body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
  text-align:center
}

h5 {
  text-align:center
}

.dropdown {
  text-align:center
}

footer {
  text-align: center;
  height:80px;
  width: 100%;
  background-image: none;
  background-repeat: repeat;
  background-attachment: scroll;
  background-position: 0% 0%;
  position: fixed;
  bottom: 0pt;
  left: 0pt;
  opacity: .6;
}

.footer-contents {
  height:24px;
  width: 100%;
  margin:auto;
}

.tempimage {
    position: relative;
    height:200px;
    width:200px;
}

.cloudimage {
    position:relative;
    height:200px;
    width:250px;
}

.windimage {
    position:relative;
    height:200px;
    width:200px;
}

.tablething {
    border: 2px solid black;
}

tr, th, td, thead, tbody {
    border:3px solid black;
    text-align: center;
}

.dropdown-menu.show {
    height: 300px;
    overflow: scroll;
}
